<template>
  <div id="education_statistics">
    <div class="statistics_container">
      <chart-unit v-model="funnelDateAndCourse" :options="options">
        <funnel-chart :funnelData="funnelData"></funnel-chart>
      </chart-unit>

      <chart-unit v-model="pieDateAndCourse" :options="options">
        <pie-chart :pieData="pieData"></pie-chart>
      </chart-unit>

      <chart-unit v-model="barDateAndCourse" :options="options">
        <bar-chart :barData="barData"></bar-chart>
      </chart-unit>

      <chart-unit v-model="lineDateAndCourse" :options="options">
        <line-chart :lineData="lineData"></line-chart>
      </chart-unit>
    </div>
  </div>
</template>

<script>
import ChartUnit from "./components/ChartUnit";
import Echart from "./components/EcChart";
import FunnelChart from "./components/FunnelChart";
import PieChart from "./components/PieChart";
import BarChart from "./components/BarChart";
import LineChart from "./components/LineChart";
import { funnel, pie, bar, line } from "./components/echart";
export default {
  data() {
    return {
      date: [new Date(new Date().getTime() - 24 * 3600 * 1000), new Date()],
      funnelData: [
        { value: 5000, name: "浏览网站" },
        { value: 2000, name: "收藏" },
        { value: 1500, name: "生成订单" },
        { value: 1400, name: "支付订单" }
      ],
      pieData: [{ value: 50, name: "新用户" }, { value: 20, name: "续报" }],
      barData: {
        data: [
          [320, 332, 301, 334, 390, 330, 320],
          [120, 132, 101, 134, 90, 230, 210]
        ],
        x: ["2020高考数学", "民族舞初级", "英语基础", "美术基础", "作文提高", "初三数学暑期班", "小升初过渡"]
      },

      lineData: [[820, 932, 901, 934, 1290, 1330, 1320],[567,455,232,233,678,246,987]],
      funnelDateAndCourse: {
        date: [new Date(new Date().getTime() - 24 * 3600 * 1000), new Date()],
        course: 1
      },
      pieDateAndCourse: {
        date: [new Date(new Date().getTime() - 24 * 3600 * 1000), new Date()],
        course: 1
      },
      barDateAndCourse: {
        date: [new Date(new Date().getTime() - 24 * 3600 * 1000), new Date()],
        course: 1
      },
      lineDateAndCourse: {
        date: [new Date(new Date().getTime() - 24 * 3600 * 1000), new Date()],
        course: 1
      },

      options: [
        {
          label: "2020高考冲刺",
          value: 1
        },
        {
          label: "帝王成长超级班",
          value: 2
        },
        {
          label: "文言文培训",
          value: 3
        }
      ]
    };
  },
  mounted() {},
  methods: {
    change(val, obj) {
      alert(1);
    }
  },
  components: {
    ChartUnit,
    Echart,
    FunnelChart,
    PieChart,
    BarChart,
    LineChart
  },
  watch: {
    date(val) {}
  }
};
</script>

<style lang="scss" scoped>
@import "@/styles/mixin.scss";
@import "@/styles/variables.scss";
#education_statistics {
  @include layout;
}

.statistics_container {
  padding: 20px;
  @include display(space-around) {
    flex-wrap: wrap;
  }
  .chart_main {
    width: 100%;
    height: 100%;
  }
}
</style>



